.search__logo {
  display: block;
  position: relative;
}

.search__logo:after {
  content: "";
  display: block;
  position: absolute;
}

.search__logo--big:after {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
